.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  width: max(78%, 342px);
  margin: auto;
  font-size: 1.0em;
  color: rgb(59, 65, 71);
  border-bottom: 1px solid black;
}

.logo-big-container {
  text-align: center;
}

.logo-big-container img {
  width: 80%;
  height: auto;
}

@media screen and (max-width: 800px) {
  .navbar {
    flex-direction: column;
  }
}

.nav-item {
  text-decoration: none;
  background-color: #00a4aa;
  background-image: linear-gradient(45deg, #00a4aa, #00073b);
  background-size: 100%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  width: 20%;
}

/* Allinea a destra il testo negli ultimi due figli */
.nav-item:nth-last-child(-n+2) {
  text-align: right;
}

/* Rimuove la sottolineatura al passaggio del mouse */
.nav-item:hover {
  text-decoration: none;
  color: rgb(39, 39, 39);
}

.icon {
  margin-left: 0.5em;
  width: 20px;
}

.logo-small-view {
  width: 80%;
}

.dropcase {
  position: relative;
  width: 100%;
  text-align: center;
}

/* Imposta il display su inline-block per adattarsi alla larghezza automatica */
.menu-button {
  width: 100%; 
  display: inline-block; 
  background-color: rgb(255, 255, 255);
  outline: none;
  border: none;
  font-size: 1.4rem;
  margin: 2% 0 2% 0;
}

.menu-button:focus {
  outline: none;
}

.menu-button:hover {
  cursor: pointer;
}
  
.dropdown-content {
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #f1f1f1;
  width: 100%;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 10;
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}
  
  /* Change color of dropdown links on hover */
.dropdown-content a:hover {background-color: #00a4aa;}
  
  /* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {display: block;}



  